export class NumericalMotion {
    public t: number = 0.0;
    constructor() {
        //
    }
    public scale(r: number) { return (Math.sin(this.t) + 5.0) * 0.2 * r; }
    // elastic bounce effect at the beginning
    public easeInElastic(t: number) { return (.04 - .04 / t) * Math.sin(25 * t) + 1; }
    // https://joshondesign.com/2013/03/01/improvedEasingEquations
    public easeOutElastic2(t: number) { var p = 0.3; return Math.pow(2, -10 * t) * Math.sin((t - p / 4) * (2 * Math.PI) / p) + 1; }
}
