import { maskOutContextByShape } from '../../lib';
import { Model } from '..';
export class ImageWrapper {
  public model: Model;
  public imgAppended: HTMLImageElement;
  public dataFilter: any = '';
  public img: HTMLImageElement;
  public imgString: string = '';
  public imgCroppingParam: any;

  public imgOriginal: HTMLImageElement;
  public imgOriginalString: string = '';
  public imgOriginalCroppingParam: any;
  public useModifiedImage: boolean = true;


  public croppingDataTemp: any | undefined = undefined;
  constructor(model: Model) {
    this.model = model;
  }
  public projectionToImageSpace(x: number, y: number) {
    // These are needed with image cropped by canvas.
    /*
    const bounding = this.imageAnnoApp.graphicsCoreWrapper.canvasWrapper.aCanvas.canvasView.getBoundingClientRect();
    e.x *= this.imageAnnoApp.implementations.app.model.imageWrapper.img.width / bounding.width;
    e.y *= this.imageAnnoApp.implementations.app.model.imageWrapper.img.height / bounding.height;
    */
    return [
      x * (this.img.width / this.imgAppended.width),
      y * (this.img.height / this.imgAppended.height)
    ];
  }

  public updateEffecting(d: any) {
    this.dataFilter = d;
  }
  public initImageByImg(img: HTMLImageElement, dataCropping: string, imgOriginal: HTMLImageElement, colorCover = '#1D2730') {
    this.img = img;
    this.imgString = img.src;
    this.imgOriginal = imgOriginal;
    this.imgOriginalString = imgOriginal.src;
    // console.log('img from anno',img, imgOriginal);

    this.imgCroppingParam = JSON.parse(dataCropping);
    this.croppingDataTemp = this.imgCroppingParam

    this.model.app.param.colorCover = colorCover;

    if (this.model.app.isLoaded) {
      this.model.app.implementations.imageAnnoApp.scene.paramsEditing.updateDataCropping(this.croppingDataTemp);
      this.updateImg(img, colorCover);
    }
  }
  public updateImg(img: HTMLImageElement, colorCover = '#1D2730') {
    this.imgAppended = new Image();
    this.imgAppended.src = img.src;
    this.imgAppended.style.pointerEvents = 'none';
    this.imgAppended.style.width = '100%';
    this.imgAppended.style.height = '100%';

    // this.imgAppended.style.backgroundColor = 'rgba(255,255,255,0.5)';
    // this.imgAppended.style.background = 'repeating-conic-gradient(#cccccc 0% 25%, #ffffff 0% 50%) 50% / 10px 10px';
    // this.imageAppend.style.position = 'absolute';

    /* These are code for cropping it by css.
    // start
    const { x, y, width, height } = this.croppingDataTemp;
    const originalHeight = img.naturalHeight;
    const originalWidth = img.naturalWidth;
    const top = y;
    const left = x;
    const bottom = originalHeight - height - top;
    const right = originalWidth - width - left;

    // NOTE: use percent values.
    // ????: cannot use pixel values: the container client rect value is not reliable, so cannot calculate update-to-date pixel values
    const topInPercent = `${top / originalHeight * 100}%`;
    const bottomInPercent = `${bottom / originalHeight * 100}%`;
    const leftInPercent = `${left / originalWidth * 100}%`;
    const rightInPercent = `${right / originalWidth * 100}%`;

    // use CSS clip-path to crop the original image:
    this.imgAppended.style.clipPath = `inset(${topInPercent} ${rightInPercent} ${bottomInPercent} ${leftInPercent})`;

    const scaleX = originalWidth / width;
    const scaleY = originalHeight / height;

    // use CSS transform to pan and zoom to the cropped area:
    this.imgAppended.style.transformOrigin = `${leftInPercent} ${topInPercent}`; // very important
    this.imgAppended.style.transform = `translate(-${leftInPercent}, -${topInPercent}) scale(${scaleX}, ${scaleY})`;

    // the original image takes extra space than the displayed area, which causes overflow. so hidden the extra area on parent:
    this.model.modelViewWrapper.div.style.overflow = 'hidden';
    // end
    */

    this.imgAppended.onload = () => {
      this.updateMaskForCroppingShape(img, colorCover);

      this.model.modelViewWrapper.div.append(this.imgAppended);

      // compute the scale factor for node based on the ratio between actual size of image and the div displayed
      const ratio = ((img.width / this.imgAppended.width) + (img.height / this.imgAppended.height)) * 0.5;
      if (this.model.app.implementations.imageAnnoApp) {
        this.model.app.implementations.imageAnnoApp.graphicsCoreWrapper.scaleScreenByRatioImageSize = ratio < 1 ? 1 : ratio;
      }

      // this.model.modelViewWrapper.divNGraphicsHost.style.background = '#0ff0f0';
      // this.model.modelViewWrapper.divHost.style.background = '#ff0000';

      // this.model.modelViewWrapper.divNGraphicsHost.style.display = 'flex';
      // this.model.modelViewWrapper.divNGraphicsHost.style.position = 'relative';
      // this.model.modelViewWrapper.divNGraphicsHost.style.justifyContent = 'center';

      // These are needed with image cropped by canvas.
      this.model.app.implementations.imageAnnoApp.graphicsCoreWrapper.resizeCanvas(img.width, img.height);

      // These are needed with image cropped by css.
      // this.model.app.implementations.imageAnnoApp.graphicsCoreWrapper.resizeCanvas(width, height);

      // this.model.app.implementations.imageAnnoApp.graphicsCoreWrapper.canvasWrapper.aCanvas.divHost.style.width = `${width}px`;
      // this.model.app.implementations.imageAnnoApp.graphicsCoreWrapper.canvasWrapper.aCanvas.divHost.style.height = `${height}px`;

      // this.model.modelViewWrapper.div.style.textAlign = 'center';
      // this.model.modelViewWrapper.div.style.width = '100vw';
      // this.model.modelViewWrapper.div.style.height = '100vh';
      // this.model.modelViewWrapper.div.style.position = 'absolute';

      if (this.model.app.implementations.imageAnnoApp) {
        this.model.app.implementations.imageAnnoApp.graphicsCoreWrapper.canvasWrapper.isActive = true;
      }
      this.model.ioWrapper.emitAction.emitAction('ready@markup');

      // we do not need to execute it again after this initiation.
      this.imgAppended.onload = () => { }
    };
  }
  public toCroppedImg() {
    this.useModifiedImage = true;
    this.imgAppended.src  = this.imgString;
    this.toModifiedImgCroppingParam();
    this.model.ioWrapper.emitAction.emitAction('update-cropping@' + JSON.stringify(this.imgCroppingParam));
    this.model.ioWrapper.emitAction.emitAction('update-effecting@' + JSON.stringify(this.dataFilter));
    this.model.ioWrapper.emitAction.emitAction(`update-container-size@`);
  }
  public toOriginalImg() {
    this.useModifiedImage = false;
    this.imgAppended.src  = this.imgOriginalString;
    this.toOriginalImgCroppingParam();
    this.model.ioWrapper.emitAction.emitAction('update-cropping@' + JSON.stringify(this.imgOriginalCroppingParam));
    this.model.ioWrapper.emitAction.emitAction('update-effecting@' +
    JSON.stringify(
      {
        typePreset : 'normal',
        filter : {
          interpolation: 1.0,
          blur: 0.0,
          sepia : 0,
          contrast : 1,
          brightness : 1,
          saturate : 1,
          hueRotate : 0,
        },
        tint : { },
      }
      ));
    this.model.ioWrapper.emitAction.emitAction(`update-container-size@original`);
  }
  public toOriginalImgCroppingParam() {
    this.imgOriginalCroppingParam = {
        aspectRatio: 'original',
        x: 0,
        y: 0,
        width: this.imgOriginal.width,
        height: this.imgOriginal.height,
        rotation: 0,
        scale: 1,
        shape: 'rectangle',
    };

    this.img.width = this.imgOriginalCroppingParam.width;
    this.img.height = this.imgOriginalCroppingParam.height;
    if (this.model.app.implementations.imageAnnoApp) {
      this.model.app.implementations.imageAnnoApp.scene.paramsEditing.updateDataCropping(this.imgOriginalCroppingParam);
      this.model.app.implementations.imageAnnoApp.graphicsCoreWrapper.resizeCanvas(this.imgOriginal.width, this.imgOriginal.height);
    }

    this.croppingDataTemp = this.imgOriginalCroppingParam;
  }
  public toModifiedImgCroppingParam() {
    this.img.width = this.imgCroppingParam.width;
    this.img.height = this.imgCroppingParam.height;

    if (this.model.app.implementations.imageAnnoApp) {
      this.model.app.implementations.imageAnnoApp.scene.paramsEditing.updateDataCropping(this.imgCroppingParam);
      this.model.app.implementations.imageAnnoApp.graphicsCoreWrapper.resizeCanvas(this.imgCroppingParam.width, this.imgCroppingParam.height);
    }
    this.croppingDataTemp = this.imgCroppingParam;

  }
  private updateMaskForCroppingShape(img: HTMLImageElement, colorCover: string) {
    const canvas = document.createElement('canvas');
    canvas.style.width = '100%';
    canvas.style.height = '100%';
    canvas.width = img.width;
    canvas.height = img.height;
    canvas.style.position = 'absolute';
    this.model.modelViewWrapper.div.append(canvas);
    const ctx = canvas.getContext('2d') as CanvasRenderingContext2D;
    ctx.fillStyle = colorCover;
    ctx.beginPath();
    ctx.rect(0, 0, canvas.width, canvas.height);
    ctx.closePath();
    ctx.fill();
    maskOutContextByShape(ctx, this.croppingDataTemp.shape, 0, 0, this.croppingDataTemp.width, this.croppingDataTemp.height, false);
  }
}
