import { MouseEventData, KeyboardEventData, TouchEventDataList } from '../lib';
import { DynamicImport } from './Core/DynamicImport';
import { AppImageDrawing } from '..';
export class Addon {
  app: AppImageDrawing;
  dynamicImport: DynamicImport;
  // eslint:disable-next-line:no-any
  visualizationJSAPI: any;
  // eslint:disable-next-line:no-any
  visualizationMapJSAPIExternal: any;
  // eslint:disable-next-line:no-any
  drawingBlock: any;
  // eslint:disable-next-line:no-any
  mapfilter: any;
  // eslint:disable-next-line:no-any
  mapticon: any;
  // eslint:disable-next-line:no-any
  tourTool: any;
  // eslint:disable-next-line:no-any
  quickCommander: any;
  // eslint:disable-next-line:no-any
  smartCommander: any;
  // eslint:disable-next-line:no-any
  smartDrawing: any;
  // eslint:disable-next-line:no-any
  voiceController: any;
  private _stopPropagation: boolean = false;
  private _enablePan: boolean = true;
  constructor(app: AppImageDrawing) {
    this.app = app;
    this.dynamicImport = new DynamicImport(this);
  }
  public get enablePan() {
    if (this.mapticon) {
      this._enablePan = this.mapticon.enablePan;
    }
    return this._enablePan;
  }
  public set enablePan(v: boolean) {
    this._enablePan = v;
  }
  public get stopPropagation() {
    return this._stopPropagation;
  }
  public set stopPropagation(v: boolean) {
    this._stopPropagation = v;
  }
  public MouseEventController(m: MouseEventData): void {
    if (!this.stopPropagation) {
      if (this.mapticon) {
        this.mapticon.MouseEventController(m);
      }
      if (this.tourTool) {
        this.tourTool.MouseEventController(m);
      }
    }
    // if (this.model.app.implementation ) {this.model.app.implementation.MouseEventController(m); }
  }
  public KeyboardEventController(k: KeyboardEventData): void {
    //
  }
  public TouchEventController(t: TouchEventDataList): void {
    //
  }
  // public GestureEventController(j: GestureEventData): void {
  //   //
  // }
  public Resize(width: number, height: number) {
    if (this.mapfilter) {
      this.mapfilter.Resize(width, height);
    }
  }
  // ......................................................................
  public async InitMapticon() {
    return this.dynamicImport.initMapticon();
  }
  public async InitVisualizationJSAPI() {
    return this.dynamicImport.initVisualizationJSAPI();
  }
  public async InitVisualizationExternalGLJSAPI() {
    return this.dynamicImport.initVisualizationExternalGLJSAPI();
  }
  public async InitDrawingBlock() {
    return this.dynamicImport.initDrawingBlock();
  }
  public async InitMapfilter() {
    return this.dynamicImport.initMapfilter();
  }
  public async InitQuickCommander() {
    return this.dynamicImport.initQuickCommander();
  }
  public async InitSmartCommander() {
    return this.dynamicImport.initSmartCommander();
  }
  public async InitSmartDrawing() {
    return this.dynamicImport.initSmartDrawing();
  }
  public async InitVoiceController() {
    return this.dynamicImport.initVoiceController();
  }
}
