import { EventListenerType, PressType } from '../../../../lib';
import { KeyboardEventData } from '../../../../../../lib/EventData/KeyboardEventData';
import { Model } from '../../..';
export class KeyboardInteraction {
  public model: Model;
  public MouseEventDataPre: KeyboardEventData;
  public type: EventListenerType;
  public isUp: boolean;
  constructor(model: Model, type: EventListenerType = EventListenerType.JSAPI4X) {
    // bind event listeners first
    this.keyDown = this.keyDown.bind(this);
    this.keyUp = this.keyUp.bind(this);
    this.model = model;
    this.type = type;
    this.isUp = false;

    this.init();
    this.MouseEventDataPre = new KeyboardEventData();
  }
  public init() {
    if (typeof document === 'undefined') {
      return;
    }
    document.addEventListener('keydown', this.keyDown);
    document.addEventListener('keyup', this.keyUp);
  }
  public destroy() {
    if (typeof document === 'undefined') {
      return;
    }
    document.removeEventListener('keydown', this.keyDown);
    document.removeEventListener('keyup', this.keyUp);
  }
  public keyDown(k: KeyboardEvent) {
    const kEventData: KeyboardEventData = this.commonEventBuilder(k);
    kEventData.PRESS_TYPE = PressType.Down;
    this.keyEventFromDocument(kEventData);
  }
  public keyUp(k: KeyboardEvent) {
    const kEventData: KeyboardEventData = this.commonEventBuilder(k);
    kEventData.PRESS_TYPE = PressType.Up;

    // NOTE: Only respond to `shift` and `ctrl`
    if (!kEventData.hasShiftKey || !kEventData.hasCtrlKey) {
      this.keyEventFromDocument(kEventData);
    }
  }
  // ............................................................. builder
  public commonEventBuilder(k: KeyboardEvent): KeyboardEventData {
    const kEvent: KeyboardEventData = new KeyboardEventData();
    kEvent.preEvent = this.MouseEventDataPre;
    kEvent.key = k.key;
    kEvent.keyCode = k.keyCode;
    kEvent.code = k.code;
    kEvent.hasCtrlKey = k.ctrlKey;
    kEvent.hasShiftKey = k.shiftKey;
    kEvent.hasAltKey = k.altKey;
    kEvent.hasMetaCommand = k.metaKey;
    this.MouseEventDataPre = kEvent;
    return kEvent;
  }
  public keyEvent(keyboardEventData: KeyboardEventData) {
    this.model.controllerWrapper.keyboardEventController(keyboardEventData);
  }
  public keyEventFromDocument(keyboardEventData: KeyboardEventData) {
    this.model.controllerWrapper.keyboardEventControllerFromDocument(keyboardEventData);
  }
}
