import { IOWrapper } from '..';
export const delimiter = '@';
export class EmitAction {
  public ioWrapper: IOWrapper;
  constructor(ioWrapper: IOWrapper) {
    this.ioWrapper = ioWrapper;
  }
  public emitAction(data: string) {
    if (!this.ioWrapper.model.app.emitAction) {
      return;
    }
    this.ioWrapper.model.app.emitAction(data);
  }
  public getNumLayers() {
    if (!this.ioWrapper.model.app.getNumLayers) {
      return;
    }
    this.ioWrapper.model.app.getNumLayers();
  }
  public localizedString(data: string) {
    if (!this.ioWrapper.model.app.localizedString) {
      return;
    }
    this.ioWrapper.model.app.localizedString(data);
  }
}
