import { NVector3, isOverlap } from './NVector3';
// // https://en.wikipedia.org/wiki/Ramer%E2%80%93Douglas%E2%80%93Peucker_algorithm
// // reference https://gist.github.com/adammiller/826148 /
export class DouglasPeucker {
    public vs: NVector3[];
    public ls: NLine[];
    public static simplifyPath(vs: NVector3[], tolerance: number) {
        let arr: NVector3[] = this.computeDouglasPeucker(vs, tolerance);
        // always have to push the very last point on so it doesn't get left off
        arr.push(vs[vs.length - 1]);
        return arr;

    }
    private static computeDouglasPeucker(vs: NVector3[], tolerance: number) {
        if (vs.length <= 2) {
            return [vs[0]];
        }
        let returnPoints: NVector3[] = [],
            // make line from start to end
            ln = new Line(  vs[0], vs[vs.length - 1]),
            // find the largest distance from intermediate poitns to this line
            maxDistance = 0,
            maxDistanceIndex = 0,
            p;
        for (var i = 1; i <= vs.length - 2; i++) {
            var distance = ln.distanceToPoint(vs[i]);
            if (distance > maxDistance) {
                maxDistance = distance;
                maxDistanceIndex = i;
            }
        }
        // check if the max distance is greater than our tollerance allows
        if (maxDistance >= tolerance) {
            p = vs[maxDistanceIndex];
            ln.distanceToPoint(p);
            // include this point in the output
            returnPoints = returnPoints.concat(this.computeDouglasPeucker(vs.slice(0, maxDistanceIndex + 1), tolerance));
            // returnPoints.push( points[maxDistanceIndex] );
            returnPoints = returnPoints.concat(this.computeDouglasPeucker(vs.slice(maxDistanceIndex, vs.length), tolerance));
        } else {
            // ditching this point
            p = vs[maxDistanceIndex];
            ln.distanceToPoint(p);
            returnPoints = [vs[0]];
        }
        return returnPoints;
    }
    constructor() {
        /* */
    }
    public computeDouglasPeucker(vs: NVector3[], tolerance: number) {
        if (vs.length <= 2) {
            return [vs[0]];
        }
        let returnPoints: NVector3[] = [],
            // make line from start to end
            ln = new Line(vs[0], vs[vs.length - 1]),
            // find the largest distance from intermediate poitns to this line
            maxDistance = 0,
            maxDistanceIndex = 0,
            p;
        for (var i = 1; i <= vs.length - 2; i++) {
            var distance = ln.distanceToPoint(vs[i]);
            if (distance > maxDistance) {
                maxDistance = distance;
                maxDistanceIndex = i;
            }
        }
        // check if the max distance is greater than our tollerance allows
        if (maxDistance >= tolerance) {
            p = vs[maxDistanceIndex];
            ln.distanceToPoint(p);
            // include this point in the output
            returnPoints = returnPoints.concat(this.computeDouglasPeucker(vs.slice(0, maxDistanceIndex + 1), tolerance));
            // returnPoints.push( points[maxDistanceIndex] );
            returnPoints = returnPoints.concat(this.computeDouglasPeucker(vs.slice(maxDistanceIndex, vs.length), tolerance));
        } else {
            // ditching this point
            p = vs[maxDistanceIndex];
            ln.distanceToPoint(p);
            returnPoints = [vs[0]];
        }
        return returnPoints;
    }

}
class Line {
    public p0: NVector3;
    public p1: NVector3;
    constructor(p0: NVector3, p1: NVector3) {
        this.p0 = p0;
        this.p1 = p1;
    }
    public distanceToPoint(p: NVector3) {
        // slope
        let m = (this.p1.y - this.p0.y) / (this.p1.x - this.p0.x),
            // y offset
            b = this.p0.y - (m * this.p0.x),
            d = [];
        // distance to the linear equation
        d.push(Math.abs(p.y - (m * p.x) - b) / Math.sqrt(Math.pow(m, 2) + 1));
        // distance to p0
        d.push(Math.sqrt(Math.pow((p.x - this.p0.x), 2) + Math.pow((p.y - this.p0.y), 2)));
        // distance to p1
        d.push(Math.sqrt(Math.pow((p.x - this.p1.x), 2) + Math.pow((p.y - this.p1.y), 2)));
        // return the smallest distance
        return d.sort((aa, bb) => { return (aa - bb); })[0]; // causes an array to be sorted numerically and ascending;
    }
}
export class NLine {
    public p0: NVector3;
    public p1: NVector3;
    public static isOverlap(ln: NLine, other: NLine): boolean {
        if (isOverlap(ln.p0, other.p0) && isOverlap(ln.p1, other.p1)) {
            return true;
        } else if (isOverlap(ln.p0, other.p1) && isOverlap(ln.p1, other.p0)) {
            return true;
        } else {
            return false;
        }
    }
    constructor(p0: NVector3, p1: NVector3) {
        this.p0 = p0; this.p1 = p1;
    }
}
