import { ClickType } from '../Enum/EnumCommon';
export class MouseEventData {
  public x = -1;
  public y = -1;
  public z = -1;
  public preX = -1;
  public preY = -1;
  public preZ = -1;
  public yGLPicking: number;
  // public command: ActiveCommandBase; // = new CActiveCommandNone(this.app.activeCommands);
  public CLICK_TYPE: ClickType = ClickType.Move;
  public isDown = false;
  public isDoubleClick = false;
  public isMove = false;
  public pressedShift = false;
  public pressedAlt = false;
  public pressedCtrl = false;
  public lat = -1;
  public long = -1;
  public wheel = 0;
  public preWheel = 0;
  public isActive = true;
  public native: MouseEvent;
  constructor() {
    //
  }
  public deepCopy() {
    const mEvent = new MouseEventData();
    mEvent.x = this.x;
    mEvent.y = this.y;
    mEvent.z = this.z;
    mEvent.preX = this.preX;
    mEvent.preY = this.preY;
    mEvent.preZ = this.preZ;
    mEvent.pressedAlt = this.pressedAlt;
    mEvent.pressedShift = this.pressedShift;
    mEvent.pressedCtrl = this.pressedCtrl;
    mEvent.isDoubleClick = this.isDoubleClick;
    mEvent.isDown = this.isDown;
    mEvent.isMove = this.isMove;
    mEvent.lat = this.lat;
    mEvent.long = this.long;
    mEvent.wheel = this.wheel;
    mEvent.preWheel = this.preWheel;
    mEvent.yGLPicking = this.yGLPicking;
    mEvent.CLICK_TYPE = this.CLICK_TYPE;
    mEvent.native = this.native;
    return mEvent;
  }
}
