  constructor(model: Model) {
    this.model = model;

    this.mouseEventDataPre = new MouseEventData();

    this.mouseEvent = new MouseEventNative(this);
    this.initNativeMouseWheelEvent(this.model.modelViewWrapper.div);
  }
  // ................................................................ event from native
  public initNativeMouseWheelEvent(div: HTMLElement) {
    div.onwheel = (e: WheelEvent) => {
      this.mouseWheel(e, e.deltaY);
    };
  }
  public mouseDown(e: MouseEvent) {
    if(!this.isInsideCropShape(e)) { return; }

    this.click = true;
    this.down = true;
    const mEvent = this.commonEventBuilder(e);
    mEvent.CLICK_TYPE = ClickType.Down;
    mEvent.isDown = true;
    this.mouseEvenEmitting(mEvent);
  }
  public mouseClick(e: MouseEvent) {
    if(!this.isInsideCropShape(e)) { return; }

    // if (!this.click) {
    //   return;
    // }
    const mEvent = this.commonEventBuilder(e);
    if (e.button === 0) {
      mEvent.CLICK_TYPE = ClickType.Left;
    } else if (e.button === 1) {
      mEvent.CLICK_TYPE = ClickType.Middle;
    } else if (e.button === 2) {
      mEvent.CLICK_TYPE = ClickType.Right;
    }
    this.mouseEvenEmitting(mEvent);
  }
  public mouseDoubleClick(e: MouseEvent) {
    if(!this.isInsideCropShape(e)) { return; }

    const mEvent = this.commonEventBuilder(e);
    mEvent.CLICK_TYPE = ClickType.Double;
    this.mouseEvenEmitting(mEvent);
  }
  public mouseDrag(e: MouseEvent) {
    this.click = false;
    const mEvent = this.commonEventBuilder(e);
    mEvent.CLICK_TYPE = ClickType.Drag;
    mEvent.isDown = true;
    if (e.buttons === 1) {
      mEvent.CLICK_TYPE = ClickType.Drag;
    } else if (e.buttons === 4) {
      mEvent.CLICK_TYPE = ClickType.DragMiddle;
    } else if (e.buttons === 2) {
      mEvent.CLICK_TYPE = ClickType.DragRight;
    }
    this.mouseEvenEmitting(mEvent);
  }
  public mouseMove(e: MouseEvent) {
    if(!this.isInsideCropShape(e)) { return; }

    if (this.down) {
      this.mouseDrag(e);
      return;
    }
    const mEvent = this.commonEventBuilder(e);
    mEvent.CLICK_TYPE = ClickType.Move;
    mEvent.isDown = false;
    this.mouseEvenEmitting(mEvent);
  }
  public mouseUp(e: MouseEvent) {
    this.down = false;
    const mEvent = this.commonEventBuilder(e);
    mEvent.CLICK_TYPE = ClickType.Up;
    mEvent.isDown = false;
    this.mouseEvenEmitting(mEvent);
  }
  public mouseWheel(e: MouseEvent, v: number) {
    if(!this.isInsideCropShape(e)) { return; }

    const mEvent = this.commonEventBuilder(e);
    mEvent.wheel = v;
    mEvent.CLICK_TYPE = ClickType.Wheel;
    this.mouseEvenEmitting(mEvent);
  }
