  public appendUpEvent() {
    document.onmouseup = (e: MouseEvent) => {
      const mEvent = this.commonEventBuilder(e);
      if (e.button === 0) {
        mEvent.CLICK_TYPE = ClickType.Left;
      } else if (e.button === 1) {
        mEvent.CLICK_TYPE = ClickType.Middle;
      } else if (e.button === 2) {
        mEvent.CLICK_TYPE = ClickType.Right;
      }
      mEvent.isDown = false;
      // mEvent.FROM = FROM.NATIVE;
      mEvent.CLICK_TYPE = ClickType.Up;
      this.mouseEvenEmitting(mEvent);
    };
  }
  get isEnableZoomByDoubleClick() {
    return this.isEnableZoomByDoubleClickValue;
  }
  set isEnableZoomByDoubleClick(v: boolean) {
    if (v) {
      this.timer = setTimeout(() => {
        this.isEnableZoomByDoubleClickValue = v;
      }, 1000);
    } else {
      this.isEnableZoomByDoubleClickValue = v;
    }
  }
