  public commonEventBuilder(e: MouseEvent): MouseEventData {
    const mEvent: MouseEventData = new MouseEventData();
    mEvent.preX = this.mouseEventDataPre.x;
    mEvent.preY = this.mouseEventDataPre.y;
    // mEvent.premEvent = this.mouseEventDataPre;

    mEvent.pressedShift = e.shiftKey;
    mEvent.pressedAlt = e.altKey;
    mEvent.pressedCtrl = e.ctrlKey;

    mEvent.x = e.offsetX;
    mEvent.y = e.offsetY;
    mEvent.preWheel = this.mouseEventDataPre.wheel;
    mEvent.wheel = 0;
    mEvent.native = e;
    this.mouseEventDataPre = mEvent;
    this.e = mEvent;
    return mEvent;
  }
  get stopPropagation() {
    return this._stopPropagation;
  }
  set stopPropagation(v: boolean) {
    this._stopPropagation = v;
  }
  public mouseEvenEmitting(mouseEventData: MouseEventData) {
    if (this.model.appCoreWrapper.appControllerStatus.mode === InteractionMode.ObserveViewer) {
      return;
    }
    // this.stopPropagation = false;
    this.mouseEventEmittingForViewController(mouseEventData);
    if (this._stopPropagation) {
      return;
    }
    this.mouseEventEmittingForModel(mouseEventData);
  }
  public mouseEventEmittingForViewController(mouseEventData: MouseEventData) {
    if (this.mouseInteractionUI) {
      this.mouseInteractionUI.mouseInteractionMenu(mouseEventData);
    }
  }
  public mouseEventEmittingForModel(mouseEventData: MouseEventData) {
    // if (mouseEventData.CLICK_TYPE === ClickType.Left) {
    // console.log(mouseEventData.CLICK_TYPE)
    // }
    this.model.controllerWrapper.mouseEventController(mouseEventData);
  }
  public destroy() {
    this.mouseEvent.removeEvent()
  }
  private isInsideCropShape(e: MouseEvent) {
    if (!this.model.app.implementations.imageAnnoApp.scene.paramsEditing.isInsideCropShape(e.offsetX, e.offsetY)) {
      e.stopPropagation();
      return false;
    } else {
      return true;
    }
  }
