import { KeyboardEventData } from '../../../../lib/EventData/KeyboardEventData';
import { MouseEventData } from '../../../../lib/EventData/MouseEventData';
import { TouchEventDataList } from '../../../../lib/EventData/TouchEventData';
import { Model } from '..';
export class ControllerWrapper {
  public model: Model;
  constructor(model: Model) {
    this.model = model;
  }
  public mouseEventController(m: MouseEventData): void {
    if (this.model.app.implementations) {
      this.model.app.implementations.mouseEventController(m);
    }
    if (this.model.app.addon) {
      // this.model.app.addon.mouseEventController(m);
    }
  }
  public keyboardEventController(k: KeyboardEventData): void {
    if (this.model.app.implementations) {
      this.model.app.implementations.keyboardEventController(k);
    }
    if (this.model.app.addon) {
      // this.model.app.addon.keyboardEventController(k);
    }
  }
  public keyboardEventControllerFromDocument(k: KeyboardEventData): void {
    if (this.model.app.implementations) {
      this.model.app.implementations.keyboardEventControllerFromDocument(k);
    }
  }
  public touchEventController(t: TouchEventDataList): void {
    if (this.model.app.implementations) {
      this.model.app.implementations.touchEventController(t);
    }
  }
}
