import { CompileMode } from '../../lib';
import { Model } from '..';
export class ModelViewWrapper {
  public model: Model;
  public divHost: HTMLElement;
  public divNGraphicsHost: HTMLElement;
  public div: HTMLElement;
  public divWrapper: HTMLElement;
  constructor(model: Model, id: string) {
    this.model = model;
    this.init(id);
    if (this.model.appCoreWrapper.appProperties.compileMode !== CompileMode.Production) {
      // TODO: something needed for no production
    }
  }
  private init(id: string) {

    const divNgraphics = document.createElement('div');
    this.divNGraphicsHost = divNgraphics;
    // divNgraphics.style.position = 'absolute';
    // divNgraphics.style.top = '0px';
    // divNgraphics.style.pointerEvents = 'none';
    divNgraphics.id = 'ExpressImageDrawing';
    divNgraphics.style.width = '100%';
    divNgraphics.style.height = '100%';
    // divNgraphics.style.pointerEvents = 'none';

    this.divWrapper = document.createElement('div');
    this.divWrapper.id = `Annotation-image-wrapper${this.model.app.CONFIG.VERSION_APP}`;
    this.divWrapper.style.width = '100%';
    this.divWrapper.style.height = '100%';
    this.divWrapper.style.pointerEvents = 'none';
    // this.divWrapper.style.backgroundImage = 'radial-gradient(rgb(235, 235, 235), rgba(200, 200, 200, 1))';

    this.div = document.createElement('div');
    this.div.id = `Annotation-image-${this.model.app.CONFIG.VERSION_APP}`;
    this.div.style.userSelect = 'none';
    this.div.style.width = '100%';
    this.div.style.height = '100%';
    this.div.style.zIndex = '1';
    this.div.style.position = 'relative';
    this.divWrapper.appendChild(this.div);

    divNgraphics.appendChild(this.divWrapper);

    const divHost = document.getElementById(id);
    if (divHost) {
      this.divHost = divHost;
      if (this.divHost.parentNode) {
        this.divHost.appendChild(divNgraphics);
      }
    }
  }
  public removeDivImg() {
    this.model.modelViewWrapper.div.removeChild(this.model.modelViewWrapper.div.childNodes[2]);
  }
  public removeDiv() {
     try {
       this.model.modelViewWrapper.divNGraphicsHost.parentNode.removeChild(this.model.modelViewWrapper.divNGraphicsHost);
     } catch (error) {
       console.debug(error);
     }
  }
  public initPost() {
    // TODO:
  }
}
