import { InteractionMode } from '../Enum/EnumCommon';
export class InputControllerStatus {
    public clickLeft: boolean;
    public clickMiddle: boolean;
    public clickRight: boolean;
    public clickDouble: boolean;
    public mouseDown: boolean;
    public mouseMove: boolean;
    public mouseDrag: boolean;
    public keyDown: boolean;
    constructor() {
        this.clickLeft = true;
        this.clickMiddle = true;
        this.clickRight = true;
        this.clickDouble = true;
        this.mouseDown = true;
        this.mouseMove = true;
        this.mouseDrag = true;
        this.keyDown = true;
    }
}
export class AppControllerStatus {
    public currentControllerStatus: InputControllerStatus;
    public drawing: InputControllerStatus;
    public interactiveViewer: InputControllerStatus;
    public viewer: InputControllerStatus;
    private _mode: InteractionMode;
    constructor(mode: InteractionMode) {
        this.currentControllerStatus = new InputControllerStatus();
        this.drawing = new InputControllerStatus();
        this.interactiveViewer = new InputControllerStatus();
        this.viewer = new InputControllerStatus();

        this.interactiveViewer.clickLeft = true;
        this.interactiveViewer.clickMiddle = false;
        this.interactiveViewer.clickRight = false;
        this.interactiveViewer.clickDouble = false;
        this.interactiveViewer.mouseDown = true;
        this.interactiveViewer.mouseMove = true;
        this.interactiveViewer.mouseDrag = false;
        this.interactiveViewer.keyDown = false;

        this.viewer.clickLeft = false;
        this.viewer.clickMiddle = false;
        this.viewer.clickRight = false;
        this.viewer.clickDouble = false;
        this.viewer.mouseDown = false;
        this.viewer.mouseMove = false;
        this.viewer.mouseDrag = false;
        this.viewer.keyDown = false;
        this.mode = mode;
    }
    public set mode(interactionMode: InteractionMode) {
        this._mode = interactionMode;
        if (interactionMode === InteractionMode.Drawing) { this.currentControllerStatus = this.drawing; }
        if (interactionMode === InteractionMode.InteractiveViewer) { this.currentControllerStatus = this.interactiveViewer; }
        if (interactionMode === InteractionMode.ObserveViewer) { this.currentControllerStatus = this.viewer; }
    }
    public get mode() {
        return this._mode;
    }
    public setAccessibility(interactionMode: InteractionMode, accessibility: InputControllerStatus) {
        if (interactionMode === InteractionMode.Drawing) {
            this.drawing.clickLeft = accessibility.clickLeft;
            this.drawing.clickMiddle = accessibility.clickMiddle;
            this.drawing.clickRight = accessibility.clickRight;
            this.drawing.clickDouble = accessibility.clickDouble;
            this.drawing.mouseDown = accessibility.mouseDown;
            this.drawing.mouseMove = accessibility.mouseMove;
            this.drawing.mouseDrag = accessibility.mouseDrag;
        }
        if (interactionMode === InteractionMode.InteractiveViewer) {
            this.currentControllerStatus = this.interactiveViewer;
        }
        if (interactionMode === InteractionMode.ObserveViewer) {
            this.currentControllerStatus = this.viewer;
        }
    }
}
