export class DownloadJSON {
    constructor(d: string, name: string) {
        this.init(d, name);
    }
    private init (d: string, name: string) {
        if (typeof document === 'undefined') { return; }
        // reference : http://jsfiddle.net/soulwire/4ooupev9/
        var data = this.encode((JSON.stringify(d, null, 4)));
        // console.log(data)
        var blob = new Blob([data], { type: 'esri/drawingTool' });
        let url = URL.createObjectURL(blob);
        var link = document.createElement('a');
        link.setAttribute('href', url);
        link.setAttribute('download', name);
        var event = document.createEvent('MouseEvents');
        event.initMouseEvent('click', true, true, window, 1, 0, 0, 0, 0, false, false, false, false, 0, null);
        link.dispatchEvent(event);
    }
    public encode(s: string) {
        let out: number[] = [];
        for (let i = 0; i < s.length; i++) { out.push(s.charCodeAt(i)); }
        return new Uint8Array(out);
    }
    public dummy() { /* */ }
}
