import { MouseInteraction } from '../MouseInteraction';
import { MouseEventBase } from './Base/MouseEventBase';
export class MouseEventNative extends MouseEventBase {

  public clickable = true;
  public disableMoveEvent = false;
  constructor(mouseInteraction: MouseInteraction) {
    super(mouseInteraction);
    this.appendEvent();
  }
  protected appendEvent() {
    const div = this.mouseInteraction.model.modelViewWrapper.div;
    div.style.pointerEvents = 'auto';
    div.addEventListener('click', this.mouseClick);
    div.addEventListener('dblclick', this.mouseDoubleClick);
    div.addEventListener('mousedown', this.mouseDown);
    div.addEventListener('mouseup', this.mouseUp);
    div.addEventListener('mousemove', this.mouseMove);

  }
  public removeEvent() {
    const div = this.mouseInteraction.model.modelViewWrapper.div;
    div.removeEventListener('click', this.mouseClick);
    div.removeEventListener('dblclick', this.mouseDoubleClick);
    div.removeEventListener('mousedown', this.mouseDown);
    div.removeEventListener('mouseup', this.mouseUp);
    div.removeEventListener('mousemove', this.mouseMove);
  }
  private mouseClick = (e: MouseEvent) => {
    if (!this.clickable) {
      this.clickable = true;
      return;
    }
    this.mouseInteraction.mouseClick(e);
  }
  private mouseDoubleClick = (e: MouseEvent) => {
    this.mouseInteraction.mouseDoubleClick(e);
  }
  private mouseDown = (e: MouseEvent) => {
    this.clickable = true;
    this.mouseInteraction.mouseDown(e);
  }
  private mouseUp = (e: MouseEvent) => {
    this.disableMoveEvent = true;
    this.mouseInteraction.mouseUp(e);
  }
  private mouseMove = (e: MouseEvent) => {
    this.clickable = false;
    if (this.disableMoveEvent) {
      this.disableMoveEvent = false;
      return
    }
    this.mouseInteraction.mouseMove(e);
  }
}



