// https://developer.mozilla.org/en-US/docs/Web/API/Touch_events
import { TouchType } from './Implementation/TouchEventNative';
import { TouchEventData, TouchEventDataList } from '../../../../../../lib/EventData/TouchEventData';
import { Model } from '../../..';
import { TouchEventBase } from './Implementation/Base/TouchEventBase';
import { TouchEventNative } from './Implementation/TouchEventNative';
export class TouchInteraction {
  public static model: Model;
  public touchEventDataPre: TouchEventDataList; // = new MouseEventData();
  public isEnable = true;
  public click = false;
  public prevent = false;
  public delay = 100;
  public timer: NodeJS.Timer | number;
  public down = false;
  public e: TouchEventData;
  public preEvent: TouchInteraction | undefined;
  public model: Model;
  public touchEvent: TouchEventBase;

  constructor(model: Model) {
    this.model = model;
    TouchInteraction.model = model;
    this.touchEvent = new TouchEventNative(this);
  }
  public commonEventBuilder(e: TouchEvent, type: TouchType): TouchEventDataList {
    const mEvent: TouchEventDataList = new TouchEventDataList();
    mEvent.TOUCH_TYPE = type;
    mEvent.build(e, type);
    this.touchEventDataPre = mEvent;
    return mEvent;
  }
  public mouseEvent(touchEventData: TouchEventDataList) {
    this.model.controllerWrapper.touchEventController(touchEventData);
  }
}
