export const getGeoArea = (vs: NVector3[]) => {
  let area = 0;
  for (let i = 0; i < vs.length - 1; i++) {
    area += (vs[i].x - vs[i + 1].x) * (vs[i].y + vs[i + 1].y);
  }
  area *= 0.5;
  return Math.abs(area);
};
export const getIndexByShortDistance = (v: NVector3, vs: NVector3[]): number[] => {
  let index = -1;
  let dis = Number.MAX_SAFE_INTEGER;
  for (let i = 0; i < vs.length; ++i) {
    const distance = getDistance(v, vs[i]);
    if (distance < dis) {
      dis = distance;
      index = i;
    }
  }
  return [index, dis];
};
export const getDistance = (v0: NVector3, v1: NVector3): number => {
  return Math.sqrt(
    (v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z)
  );
  // return NWebassemblyController.ctrller.Distance(v0.x, v0.y, v0.z, v1.x, v1.y, v1.z);
};
export const getDistanceByNum = (x0: number, y0: number, x1: number, y1: number): number => {
  return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1));
  // return NWebassemblyController.ctrller.Distance(x0, v0.y, v0.z, v1.x, v1.y, v1.z);
};
export const getDistanceByArray = (v0: number[], v1: number[]): number => {
  return Math.sqrt(
    (v0[0] - v1[0]) * (v0[0] - v1[0]) +
      (v0[1] - v1[1]) * (v0[1] - v1[1]) +
      (v0[2] - v1[2]) * (v0[2] - v1[2])
  );
};
export const getDistance2 = (v1: NVector3, v2: NVector3): number => {
  const d: number =
    (v1.x - v2.x) * (v1.x - v2.x) + (v1.y - v2.y) * (v1.y - v2.y) + (v1.z - v2.z) * (v1.z - v2.z);
  return d;
};
export const getDotProduct = (v1: NVector3, v2: NVector3): number => {
  return v1.x * v2.x + v1.y * v2.y + v1.z * v2.z;
};
export const crossProduct = (v1: NVector3, v2: NVector3): NVector3 => {
  return new NVector3(
    v1.y * v2.z - v1.z * v2.y,
    v1.z * v2.x - v1.x * v2.z,
    v1.x * v2.y - v1.y * v2.x
  );
};
export const getDivideByNum = (v0: NVector3, v1: NVector3, n = 5): NVector3[] => {
  const vs: NVector3[] = [];
  const xOff = (v1.x - v0.x) / n;
  const yOff = (v1.y - v0.y) / n;
  const zOff = (v1.z - v0.z) / n;
  for (let j = 0; j < n; ++j) {
    const x = v0.x + xOff * j;
    const y = v0.y + yOff * j;
    const z = v0.z + zOff * j;
    vs.push(new NVector3(x, y, z));
  }
  return vs;
};
