export const KMeterFromGPSPos = (gV0: NVector3, gV1: NVector3): number => {
  const R = 6378.137; // Radius of earth in KM // a = Equatorial radius (6,378.1370 km) // b = Polar radius (6,356.7523 km)
  const dLat = (gV1.y * Math.PI) / 180 - (gV0.y * Math.PI) / 180;
  const dLon = (gV1.x * Math.PI) / 180 - (gV0.x * Math.PI) / 180;
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos((gV0.y * Math.PI) / 180) *
      Math.cos((gV1.y * Math.PI) / 180) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const d = R * c;
  return d; // * 1000; // meters
};
// eslint:disable-next-line:variable-name
export const get1KMLongByGeoUnit = (long = 0, lat = 0): number => {
  const v = new NVector3(long, lat, 0);
  let addition = 0.0;
  for (let i = 0; i < 10000; ++i) {
    const dis = KMeterFromGPSPos(v, new NVector3(v.x + addition, v.y, v.z));
    if (1 < dis && dis < 1.001) {
      // console.log('result long', i, dis);
      return v.x + addition;
    }
    addition += 0.00001;
  }
  return -1;
};
// eslint:disable-next-line:variable-name
export const get1KMLatByGeoUnit = (long = 0, lat = 0): number => {
  const v = new NVector3(long, lat, 0);
  let addition = 0.0;
  for (let i = 0; i < 10000; ++i) {
    const dis = KMeterFromGPSPos(v, new NVector3(v.x, v.y + addition, v.z));
    if (1 < dis && dis < 1.001) {
      // console.log('result lat', i, dis);
      return v.y + addition;
    }
    addition += 0.00001;
  }
  return -1;
};
// eslint:disable-next-line:variable-name
export const get1KMLatLongGeoUnit = (long = 0, lat = 0): NVector3 => {
  const lng = get1KMLongByGeoUnit(long, lat);
  const la = get1KMLatByGeoUnit(long, lat);
  return new NVector3(lng, la, 0);
};
