export const getCosAngleByTwoVectors = (v0: NVector3, v1: NVector3): number => {
  return getDotProduct(normalize(v0), normalize(v1));
};
export const getAngleByTwoVectorsRadian = (v0: NVector3, v1: NVector3): number => {
  return Math.acos(getCosAngleByTwoVectors(v0, v1));
};
export const getAngleByTwoVectors = (v0: NVector3, v1: NVector3): number => {
  return (getAngleByTwoVectorsRadian(v0, v1) * 180.0) / Math.PI;
};
export const GetRotationByWorldCoordinate = (v: NVector3): number => {
  const origin = new NVector3(1, 0, 0);
  return getAngleByTwoVectors(origin, v);
};
export const getRotationByWorldCoordinateByTwo = (v0: NVector3, v1: NVector3): number => {
  // mercator ctx
  const v = v1.Sub(v0);
  const origin = new NVector3(1, 0, 0);
  if (v.y > 0) {
    return getAngleByTwoVectors(origin, v) * -1;
  }
  return getAngleByTwoVectors(origin, v);
};
