export const computeDotProducts = (vs: NVector3[]) => {
  const out = [];
  for (let i = 0; i < vs.length; ++i) {
    // let v0: NVector3 = Origin();
    // let v1: NVector3;
    if (i < vs.length - 1) {
      const v0 = new NVector3(vs[i].x, vs[i].y, vs[i].z);
      const v1 = new NVector3(vs[i + 1].x, vs[i + 1].y, vs[i + 1].z);

      v1.x -= v0.x;
      v1.y -= v0.y;
      v1.z -= v0.z;

      const dot = getDotProduct(new NVector3(1, 0, 0), normalize(v1));
      out.push(dot);
    } else {
      const v0 = new NVector3(vs[i].x, vs[i].y, vs[i].z);
      const v1 = new NVector3(vs[0].x, vs[0].y, vs[0].z);

      v1.x -= v0.x;
      v1.y -= v0.y;
      v1.z -= v0.z;

      const dot = getDotProduct(new NVector3(1, 0, 0), normalize(v1));
      out.push(dot);
    }
  }
  return out;
};
export const computeDotProductsWithCenter = (vs: NVector3[]) => {
  const cp = getCenterVecFromVecs(vs);
  const out = [];
  for (let i = 0; i < vs.length; ++i) {
    let v0: NVector3;
    let v1: NVector3;
    if (i < vs.length - 1) {
      v0 = new NVector3(vs[i].x - cp.x, vs[i].y - cp.y, vs[i].z - cp.z);
      v1 = new NVector3(vs[i + 1].x - cp.x, vs[i + 1].y - cp.y, vs[i + 1].z - cp.z);
    } else {
      v0 = new NVector3(vs[i].x - cp.x, vs[i].y - cp.y, vs[i].z - cp.z);
      v1 = new NVector3(vs[0].x - cp.x, vs[0].y - cp.y, vs[0].z - cp.z);
    }
    const dot = getDotProduct(normalize(v0), normalize(v1));
    out.push(dot);
  }
  return out;
};
