export const isOverlap = (v0: NVector3, v1: NVector3, tor = 0.1) => {
  if (getDistance(v0, v1) < tor) {
    return true;
  }
  return false;
};
export const distanceMouseWithTwoVecs = (m: NVector3, v0: NVector3, v1: NVector3): number => {
  v0.x += 0.00001;
  v0.y += 0.00001;
  const boundary = getBoundingVecs(v0, v1);
  const vec = GetCloseVec(v0, v1, m.x, m.y, boundary, true);
  return getDistance(m, vec);
};
export const GetCloseVec = (
  p0: NVector3,
  p1: NVector3,
  x: number,
  y: number,
  vecsBound: NVector3[],
  OnLine = true
): NVector3 => {
  const pp1: NVector3 = new NVector3(p0.x, p0.y, p0.z);
  const pp2: NVector3 = new NVector3(p1.x, p1.y, p1.z);
  const v1: NVector3 = pp2.Sub(pp1);
  const p: NVector3 = new NVector3(x, y, 0);
  const v2: NVector3 = p.Sub(pp1);
  v1.unitize();
  const cp = pp1.Add(v1.Mult(getDotProduct(v2, v1)));
  if (OnLine) {
    if (isInsideOfVecs(cp, vecsBound)) {
      return cp;
    }
    if (getDistance(p0, cp) < getDistance(p1, cp)) {
      return new NVector3(p0.x, p0.y, p0.z);
    }
    return new NVector3(p1.x, p1.y, p1.z);
  }
  return cp;
};
export const getBoundingVecs = (v0: NVector3, v1: NVector3): NVector3[] => {
  const listOfVec: NVector3[] = [];
  let x = 0;
  let y = 0;
  let z = 0;
  if (v0.x < v1.x) {
    x = v0.x;
  } else {
    x = v1.x;
  }
  if (v0.y < v1.y) {
    y = v0.y;
  } else {
    y = v1.y;
  }
  if (v0.z < v1.z) {
    z = v0.z;
  } else {
    z = v1.z;
  }
  const nv0 = new NVector3(x, y, z);
  if (v0.x > v1.x) {
    x = v0.x;
  } else {
    x = v1.x;
  }
  if (v0.y > v1.y) {
    y = v0.y;
  } else {
    y = v1.y;
  }
  if (v0.z > v1.z) {
    z = v0.z;
  } else {
    z = v1.z;
  }
  const nv1 = new NVector3(x, y, z);
  listOfVec.push(new NVector3(nv0.x, nv0.y, nv0.z));
  listOfVec.push(new NVector3(nv1.x, nv0.y, nv0.z));
  listOfVec.push(new NVector3(nv1.x, nv1.y, nv0.z));
  listOfVec.push(new NVector3(nv0.x, nv1.y, nv0.z));
  return listOfVec;
};
export const getBoundingVecsWithScale = (v0: NVector3, v1: NVector3, scale = 1): NVector3[] => {
  const listOfVec: NVector3[] = [];
  let x = 0;
  let y = 0;
  let z = 0;
  if (v0.x < v1.x) {
    x = v0.x;
  } else {
    x = v1.x;
  }
  if (v0.y < v1.y) {
    y = v0.y;
  } else {
    y = v1.y;
  }
  if (v0.z < v1.z) {
    z = v0.z;
  } else {
    z = v1.z;
  }
  const nv0 = new NVector3(x, y, z);
  if (v0.x > v1.x) {
    x = v0.x;
  } else {
    x = v1.x;
  }
  if (v0.y > v1.y) {
    y = v0.y;
  } else {
    y = v1.y;
  }
  if (v0.z > v1.z) {
    z = v0.z;
  } else {
    z = v1.z;
  }
  const nv1 = new NVector3(x, y, z);
  listOfVec.push(new NVector3(nv0.x, nv0.y, nv0.z));
  listOfVec.push(new NVector3(nv0.x + Math.abs(nv1.x - nv0.x) * scale, nv0.y, nv0.z));
  listOfVec.push(new NVector3(nv0.x + Math.abs(nv1.x - nv0.x) * scale, nv0.y + nv1.y, nv0.z));
  listOfVec.push(new NVector3(nv0.x, nv0.y + nv1.y, nv0.z));
  return listOfVec;
};
export const getBoundingByVecs = (vs: NVector3[]): NVector3[] => {
  const listOfVec: NVector3[] = [];
  let xMin: number = Number.MAX_SAFE_INTEGER;
  let yMin: number = Number.MAX_SAFE_INTEGER;
  let zMin: number = Number.MAX_SAFE_INTEGER;

  let xMax: number = Number.MIN_SAFE_INTEGER;
  let yMax: number = Number.MIN_SAFE_INTEGER;
  let zMax: number = Number.MIN_SAFE_INTEGER;
  for (let i = 0; i < vs.length; ++i) {
    if (xMin > vs[i].x) {
      xMin = vs[i].x;
    }
    if (yMin > vs[i].y) {
      yMin = vs[i].y;
    }
    if (zMin > vs[i].z) {
      zMin = vs[i].z;
    }

    if (xMax < vs[i].x) {
      xMax = vs[i].x;
    }
    if (yMax < vs[i].y) {
      yMax = vs[i].y;
    }
    if (zMax < vs[i].z) {
      zMax = vs[i].z;
    }
  }
  listOfVec.push(new NVector3(xMin, yMin, zMin));
  listOfVec.push(new NVector3(xMax, yMax, zMax));
  return listOfVec;
};
export const getMinMaxVecFromVecs = (vec: NVector3[]): NVector3[] => {
  let minX = Number.MAX_VALUE,
    minY = Number.MAX_VALUE,
    minZ = Number.MAX_VALUE;
  let maxX = Number.MIN_VALUE,
    maxY = Number.MIN_VALUE,
    maxZ = Number.MIN_VALUE;
  for (const v of vec) {
    if (minX > v.x) {
      minX = v.x;
    }
    if (minY > v.y) {
      minY = v.y;
    }
    if (minZ > v.z) {
      minZ = v.z;
    }
    if (maxX < v.x) {
      maxX = v.x;
    }
    if (maxY < v.y) {
      maxY = v.y;
    }
    if (maxZ < v.z) {
      maxZ = v.z;
    }
  }
  return [new NVector3(minX, minY, minZ), new NVector3(maxX, maxY, maxZ)];
};
export const removeVecsWithinDis = (vs: NVector3[], tor = 10): NVector3[] => {
  const vecs: NVector3[] = [];
  for (let i = 0; i < vs.length - 1; ++i) {
    const v0 = vs[i];
    if (v0.d === false) {
      continue;
    }
    for (let j = i + 1; j < vs.length; ++j) {
      if (getDistance(v0, vs[j]) < tor) {
        vs[j].d = false;
      }
    }
  }
  for (let i = 0; i < vs.length; ++i) {
    if (vs[i].d === true) {
      vecs.push(vs[i]);
    }
  }
  // vecs.push(vs[vs.length - 1]);
  return vecs;
};
// ...............................................................................
export const isInsideOfVecsWtihVecs = (vs: NVector3[], vecs: NVector3[]): boolean => {
  let inside = true;
  for (let i = 0; i < vs.length; i++) {
    inside = isInsideOfVecs(vs[i], vecs);
    if (!inside) {
      return false;
    }
  }
  return true;
};
export const isInsideOfVecs = (v: NVector3, vecs: NVector3[]): boolean => {
  // ray-casting algorithm based on https://github.com/substack/point-in-polygon
  // http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html
  const x = v.x,
    y = v.y;
  let inside = false;
  for (let i = 0, j = vecs.length - 1; i < vecs.length; j = i++) {
    const xi = vecs[i].x,
      yi = vecs[i].y;
    const xj = vecs[j].x,
      yj = vecs[j].y;
    const intersect = yi > y !== yj > y && x < ((xj - xi) * (y - yi)) / (yj - yi) + xi;
    if (intersect) {
      inside = !inside;
    }
  }
  return inside;
};
export const isInsideOfTwoVecs = (v: NVector3, v0: NVector3, v1: NVector3): boolean => {
  // ray-casting algorithm based on https://github.com/substack/point-in-polygon
  // http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html
  const x = v.x,
    y = v.y;

  if (v0.x < x && x < v1.x && v0.y < y && y < v1.y) {
    return true;
  }
  return false;
};
export const isInsideOfVecsByCrossing = (v: NVector3, w: number, h: number, vs: NVector3[]) => {
  if (isInsideOfVecs(new NVector3(v.x - w * 0.5, v.y - h * 0.5), vs)) {
    return true;
  }
  if (isInsideOfVecs(new NVector3(v.x + w * 0.5, v.y - h * 0.5), vs)) {
    return true;
  }
  if (isInsideOfVecs(new NVector3(v.x + w * 0.5, v.y + h * 0.5), vs)) {
    return true;
  }
  if (isInsideOfVecs(new NVector3(v.x - w * 0.5, v.y + h * 0.5), vs)) {
    return true;
  }
  return false;
};
export const ConvexHull = (vs: NVector3[]): NVector3[] => {
  vs.sort(function (a: NVector3, b: NVector3) {
    return a.x === b.x ? a.y - b.y : a.x - b.x;
  });
  const lower: NVector3[] = [];
  for (let i = 0; i < vs.length; i++) {
    while (
      lower.length >= 2 &&
      Cross(lower[lower.length - 2], lower[lower.length - 1], vs[i]) <= 0
    ) {
      lower.pop();
    }
    lower.push(vs[i]);
  }
  const upper: NVector3[] = [];
  for (let i = vs.length - 1; i >= 0; i--) {
    while (
      upper.length >= 2 &&
      Cross(upper[upper.length - 2], upper[upper.length - 1], vs[i]) <= 0
    ) {
      upper.pop();
    }
    upper.push(vs[i]);
  }
  upper.pop();
  lower.pop();
  for (let i = 0; i < upper.length; ++i) {
    lower.push(upper[i]);
  }
  return lower;
};
export const Cross = (O: NVector3, A: NVector3, B: NVector3): number => {
  return (A.x - O.x) * (B.y - O.y) - (A.y - O.y) * (B.x - O.x);
};
export const checkBoundIntersection = (vs: NVector3[], p0: NVector3, p1: NVector3) => {
  for (let i = 0; i < vs.length - 1; ++i) {
    if (checkLineIntersection(vs[i], vs[i + 1], p0, p1)) {
      return true;
    }
  }
  return false;
};
// http://jsfiddle.net/justin_c_rounds/Gd2S2/light/
export const checkLineIntersection = (p0: NVector3, p1: NVector3, p2: NVector3, p3: NVector3) => {
  // if the lines intersect, the result contains the x and y of the intersection (treating the lines as infinite) and booleans for whether line segment 1 or line segment 2 contain the point
  let denominator = 0,
    a = 0,
    b = 0,
    numerator1 = 0,
    numerator2 = 0;
  let onLine0 = false,
    onLine1 = false;
  denominator = (p3.y - p2.y) * (p1.x - p0.x) - (p3.x - p2.x) * (p1.y - p0.y);
  const result: NVector3 = new NVector3(0, 0, 0);
  if (denominator === 0) {
    return undefined;
  }
  a = p0.y - p2.y;
  b = p0.x - p2.x;
  numerator1 = (p3.x - p2.x) * a - (p3.y - p2.y) * b;
  numerator2 = (p1.x - p0.x) * a - (p1.y - p0.y) * b;
  a = numerator1 / denominator;
  b = numerator2 / denominator;

  // if we cast these lines infinitely in both directions, they intersect here:
  result.x = p0.x + a * (p1.x - p0.x);
  result.y = p0.y + a * (p1.y - p0.y);
  /*
  // it is worth noting that this should be the same as:
  x = p2.x + (b * (p3.x - p2.x));
  y = p2.x + (b * (p3.y - p2.y));
  */
  // if line1 is a segment and line2 is infinite, they intersect if:
  if (a > 0 && a < 1) {
    onLine0 = true;
  }
  // if line2 is a segment and line1 is infinite, they intersect if:
  if (b > 0 && b < 1) {
    onLine1 = true;
  }
  // if line1 and line2 are segments, they intersect if both of the above are true
  if (onLine0 && onLine1) {
    return result;
  }
  return undefined;
};
//////////////////////////////////////////////////////////////////////////////// translation Utility
export const moveVecs = (vecs: NVector3[], dir: NVector3): NVector3[] => {
  const temp: NVector3[] = [];
  for (let i = 0; i < vecs.length; ++i) {
    temp.push(moveVec(vecs[i], dir));
  }
  return temp;
};
export const moveVec = (v: NVector3, dir: NVector3): NVector3 => {
  return new NVector3(v.x + dir.x, v.y + dir.y, v.z + dir.z);
};
export const rotateVecs = (vecs: NVector3[], angle: number) => {
  const temp: NVector3[] = [];
  for (let i = 0; i < vecs.length; ++i) {
    temp.push(RotateVec(vecs[i], angle));
  }
  return temp;
};
export const RotateVec = (v: NVector3, angle: number): NVector3 => {
  const cos = Math.cos(angle * (3.14159265358979 / 180.0));
  const sin = Math.sin(angle * (3.14159265358979 / 180.0));
  const x = v.x * cos - v.y * sin;
  const y = v.x * sin + v.y * cos;
  return new NVector3(x, y, v.z);
};
export const getCenterVecFromVecs = (vecs: NVector3[]): NVector3 => {
  const nv = new NVector3(0, 0, 0);
  for (let i = 0, c = vecs.length; i < c; ++i) {
    nv.Add(vecs[i]);
  }
  nv.Mult(1.0 / vecs.length);
  return nv;
};

// intersection // https://gist.github.com/gordonwoodhull/50eb65d2f048789f9558
// https://stackoverflow.com/questions/563198/whats-the-most-efficent-way-to-calculate-where-two-line-segments-intersect/1968345#1968345
export const getVecFromIntersection = (vs: NVector3[]): NVector3[] | undefined => {
  const newVes: NVector3[] = [];
  for (let j = 0; j < vs.length - 1; ++j) {
    for (let k = j + 1; k < vs.length - 1; ++k) {
      const v = checkLineIntersection(vs[j], vs[j + 1], vs[k], vs[k + 1]);
      if (v) {
        newVes.push(v);
      }
    }
  }
  if (newVes.length === 0) {
    return undefined;
  }
  return newVes;
};
export const between = (a: number, b: number, c: number) => {
  return a - eps <= b && b <= c + eps;
};
// eslint:disable-next-line:variable-name
export const segmentIntersection = (v0: NVector3, v1: NVector3, v2: NVector3, v3: NVector3) => {
  const x =
    ((v0.x * v1.y - v0.y * v1.x) * (v2.x - v3.x) - (v0.x - v1.x) * (v2.x * v3.y - v2.y * v3.x)) /
    ((v0.x - v1.x) * (v2.y - v3.y) - (v0.y - v1.y) * (v2.x - v3.x));
  const y =
    ((v0.x * v1.y - v0.y * v1.x) * (v2.y - v3.y) - (v0.y - v1.y) * (v2.x * v3.y - v2.y * v3.x)) /
    ((v0.x - v1.x) * (v2.y - v3.y) - (v0.y - v1.y) * (v2.x - v3.x));
  if (isNaN(x) || isNaN(y)) {
    return false;
  }
  if (v0.x >= v1.x) {
    if (!between(v1.x, x, v0.x)) {
      return false;
    }
  } else {
    if (!between(v0.x, x, v1.x)) {
      return false;
    }
  }
  if (v0.y >= v1.y) {
    if (!between(v1.y, y, v0.y)) {
      return false;
    }
  } else {
    if (!between(v0.y, y, v1.y)) {
      return false;
    }
  }
  if (v2.x >= v3.x) {
    if (!between(v3.x, x, v2.x)) {
      return false;
    }
  } else {
    if (!between(v2.x, x, v3.x)) {
      return false;
    }
  }
  if (v2.y >= v3.y) {
    if (!between(v3.y, y, v2.y)) {
      return false;
    }
  } else {
    if (!between(v2.y, y, v3.y)) {
      return false;
    }
  }

  return new NVector3(x, y);
};
export const isInsideEllipse = (cv: NVector3, m: NVector3, r0: number, r1: number, threshold = 5) => {
  const x = Math.pow((m.x - cv.x),2) / Math.pow(threshold + r0, 2);
  const y = Math.pow((m.y - cv.y),2) / Math.pow(threshold + r1, 2);
  if(x + y <= 1) {
      return true;
  }
  return false;
}
export class NVector3 {
  x = 0.0;
  y = 0.0;
  z = 0.0;
  w = 0.0;
  alt = 0.0;
  d = true;
  isModified = false;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  parent: any;
  constructor(x: number, y: number, z = 0) {
    this.x = x;
    this.y = y;
    this.z = z;
  }
  public Mult(v: number): NVector3 {
    this.x *= v;
    this.y *= v;
    this.z *= v;
    return new NVector3(this.x, this.y, this.z);
  }
  public divByVal(v: number): NVector3 {
    this.x /= v;
    this.y /= v;
    this.z /= v;
    return new NVector3(this.x, this.y, this.z);
  }
  // public DivByVec(v: NVector3): NVector3 {
  //   this.x /= v.x;
  //   this.y /= v.y;
  //   this.z /= v.z;
  //   return new NVector3(this.x, this.y, this.z);
  // }
  public Add(v: NVector3): NVector3 {
    this.x += v.x;
    this.y += v.y;
    this.z += v.z;
    return new NVector3(this.x, this.y, this.z);
  }
  public Sub(v: NVector3): NVector3 {
    this.x -= v.x;
    this.y -= v.y;
    this.z -= v.z;
    return new NVector3(this.x, this.y, this.z);
  }
  public unitize(): NVector3 {
    return this.normalize();
  }
  public normalize(): NVector3 {
    const len: number = Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
    if (len < 0.0001) {
      return this;
    }
    this.x = this.x / len;
    this.y = this.y / len;
    this.z = this.z / len;
    return this;
  }
}
