import { TouchType } from '../Enum/EnumCommon';
export class TouchEventDataList {
  public native: MouseEvent;
  public eventList: TouchEventData[];
  public TOUCH_TYPE: TouchType = TouchType.Move;
  constructor() {
    this.eventList = [];
  }
  // Build(e: TouchEvent) {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public build(e: any, tType: TouchType) {
    for (let i = 0; i < e.touches.length; ++i) {
      const tEvent = new TouchEventData();
      tEvent.identifier = e.touches[i].identifier;

      tEvent.TOUCH_TYPE = tType;

      tEvent.radiusX = e.touches[i].radiusX;
      tEvent.radiusY = e.touches[i].radiusY;

      tEvent.rotationAngle = e.touches[i].rotationAngle;
      tEvent.x = e.touches[i].clientX;
      tEvent.y = e.touches[i].clientY;
      tEvent.force = e.touches[i].force;

      tEvent.color = this.colorForTouch(e.touches[i]);
      this.eventList.push(tEvent);
    }
  }
  public deepCopy() {
    // return
  }
  private colorForTouch(touch: Touch) {
    const r = touch.identifier % 16;
    const g = Math.floor(touch.identifier / 3) % 16;
    const b = Math.floor(touch.identifier / 7) % 16;
    const rs = r.toString(16); // make it a hex digit
    const gs = g.toString(16); // make it a hex digit
    const bs = b.toString(16); // make it a hex digit
    const color = '#' + rs + gs + bs;
    // console.log('color for touch with identifier ' + touch.identifier + ' = ' + color);
    return color;
  }
}
export class TouchEventData {
  public x = -1;
  public y = -1;
  public z = -1;
  public preX = -1;
  public preY = -1;
  public preZ = -1;
  public yGLPicking: number;
  public TOUCH_TYPE: TouchType = TouchType.Move;
  public identifier: number;
  public radiusX: number;
  public radiusY: number;
  public rotationAngle: number;
  public force: number;
  public color: string;

  constructor() {
    //
  }
  public deepCopy() {
    const mEvent = new TouchEventData();
    mEvent.x = this.x;
    mEvent.y = this.y;
    mEvent.z = this.z;
    mEvent.preX = this.preX;
    mEvent.preY = this.preY;
    mEvent.preZ = this.preZ;

    mEvent.yGLPicking = this.yGLPicking;
    mEvent.TOUCH_TYPE = this.TOUCH_TYPE;
    // mEvent.command = this.command;
    // mEvent.native = this.native;
    return mEvent;
  }
}
