import { ActiveCommandMode, Environment, ISvgPin } from '../lib';
import { AppImageDrawing } from '..';
export class API {
    public app: AppImageDrawing;
    constructor(app: AppImageDrawing) {
        this.app = app;
    }
    public needRevertBaseImage() {
        return !this.app.model.imageWrapper.useModifiedImage;
    }
    public onclickActiveCommand(ac: ActiveCommandMode) {
        if (this.app.implementations.imageAnnoApp) {
            this.app.implementations.imageAnnoApp.activeCommandController.setActiveCommand(ac);
        }
    }
    public onClickDestroy() {
        this.app.destroy();
    }
    public onClickPlace() {
        this.emitDataToExpressImageData();
        this.onClickDestroy();
    }
    public getAllGeometries() {
        return this.app.implementations.imageAnnoApp.geometryCommon.gcmdSelecting.getAllGeometries();
    }
    public shiftEnvironment(v: Environment) {
        this.app.param.environment = v;
        this.app.implementations.imageAnnoApp.customization.themeRenderStyle.shiftEnvironment(v);
        this.app.implementations.imageAnnoApp.geometryCommon.gcmdSupporting.renderForce();
    }
    public updateRPointPath(path: ISvgPin) {
        this.app.implementations.imageAnnoApp.customization.themeRenderStyle.updateRPointPath(path);
        return this.app.implementations.imageAnnoApp.geometryCommon.gcmdSelecting.getAllGeometries()
    }
    public emitDataToExpressImageData() {
        this.app.implementations.imageAnnoApp.io.emitAction(`update@${JSON.stringify(this.getAllGeometries())}`);
    }
    public onClickCancel() {
        this.onClickDestroy();
    }
    public onClickBlackhole() {
        this.app.implementations.imageAnnoApp.apiWrapper.environment.shiftBlackholePos();
    }
    public onClickReset() {
        // TODO:
    }
    public onClickUndo() {
        this.app.implementations.imageAnnoApp.scene.undo();
        // TODO:
    }
    public onClickRedo() {
        this.app.implementations.imageAnnoApp.scene.redo();
    }
    public onClickDownload() {
        // TODO:
    }
    public onClickCapture() {
        // TODO:
    }
    public onClickSetColor(color: string, undoNeeded: boolean = true) {
        this.app.implementations.imageAnnoApp.activeCommandController.activeColorController.setActiveColor(color, undoNeeded);
    }
    public setScaleGraphics(value: number, absolute: boolean = true, undoNeeded: boolean = true) {
        this.app.implementations.imageAnnoApp.activeCommandController.setScaleGraphics(value, absolute, undoNeeded);
    }
    public updateEffecting(d: any) {
        this.app.model.imageWrapper.updateEffecting(d);
    }
    public getCanvas() {
        return this.app.implementations.imageAnnoApp.geometryCommon.gcmdAProcessing.getCanvas();
    }
    public getDataGeometry() {
        return this.app.implementations.imageAnnoApp.geometryCommon.gcmdSelecting.getAllGeometries();
    }
    public getScaleFactor() {
        return this.app.implementations.imageAnnoApp.activeCommandController.scaleGlobal;
    }
    public onClickCropper() {
        // TODO:
    }
    public onClickRevertToOriginal() {
        // console.log(img, paramCropping)
        // this.app.updateImage(img, paramCropping);

        // console.log(this.app.implementations.imageAnnoApp.renderer.croppingMaskParm);

        // this.app.implementations.imageAnnoApp.scene.captureScene(false, parma );
        // this.app.implementations.imageAnnoApp.scene.isModifiedImage = false;
        // this.app.implementations.imageAnnoApp.geometryCommon.gcmdProcessing.removeAll(false);
        if (this.app.implementations.imageAnnoApp) {
            this.app.implementations.imageAnnoApp.geometryCommon.gcmdProcessing.resetGeometryList()
            this.emitDataToExpressImageData();

            // this.app.implementations.imageAnnoApp.scene.paramsEditing revertOriginalCroppingParam();
            // this.app.model.modelViewWrapper.useModifiedImage = false;
            this.app.model.imageWrapper.toOriginalImgCroppingParam();
            this.app.model.imageWrapper.toOriginalImg();

            // this.app.implementations.imageAnnoApp.
            // this.app.core.activeCommand.revertToOriginal();
            this.app.implementations.imageAnnoApp.scene.captureScene();
            this.app.implementations.imageAnnoApp.io.emitAction('markup@resizeDiv');
        }
    }
}
