import { ActionType, InteractionMode } from '../lib';
import { AppImageDrawing } from '..';
import { AppCoreWrapper } from './AppCoreWrapper';
import { Assets } from './AssetWrapper';
import { ControllerWrapper } from './ControllerWrapper';
import { IOWrapper } from './IOWrapper';
import { ModelViewWrapper } from './ModelViewWrapper';
import { ImageWrapper } from './ImageWrapper';
import { UI } from './UI';

/*
This Model class is a wrapper for binding necessary functions and HTML element with the Annotation Image implementation.
All data or command pipeline should go through here Model wrapper as a gateway.
Asset which has something to do with external data should be here.
IO is like a middle man for communication between IO and the Annotation Image implementation
Model View is a place for div (possibly UI) which come from outside
*/
export class Model {
  public app: AppImageDrawing;
  public assets: Assets;
  public appCoreWrapper: AppCoreWrapper;
  public modelViewWrapper: ModelViewWrapper;
  public imageWrapper: ImageWrapper;
  public controllerWrapper: ControllerWrapper;
  public ioWrapper: IOWrapper;
  public ui: UI;
  constructor(app: AppImageDrawing, id: string, accessibilityMode: InteractionMode = InteractionMode.Drawing) {
    this.app = app;
    this.app.model = this;
    this.assets = new Assets(this);
    this.ioWrapper = new IOWrapper(this);
    this.appCoreWrapper = new AppCoreWrapper(this, accessibilityMode);
    this.modelViewWrapper = new ModelViewWrapper(this, id);
    this.imageWrapper = new ImageWrapper(this);
    this.controllerWrapper = new ControllerWrapper(this);
    if (this.app.param.UI) {
      this.ui = new UI(this);
    }
    this.init();
  }
  public resize(width: number, height: number) {
    this.modelViewWrapper.div.style.width = width + 'px';
    this.modelViewWrapper.div.style.height = height + 'px';
  }
  public initPost() {
    this.modelViewWrapper.initPost();
    this.appCoreWrapper.initPost();
    this.ioWrapper.initPost();
  }
  public restart() {
    // TODO:
  }
  public destroy() {
    if (this.ui) {
      this.ui.destroy();
    }
    this.ioWrapper.destroy();
    this.modelViewWrapper.removeDiv();
  }
  public pushAction(v: string | { type: ActionType | string; payload: unknown }) {
    this.ioWrapper.stateProcessing.pushAction(v);
  }
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public pushTheme(v: any) {
    this.app.implementations.pushTheme(v);
  }
  private init() {
    // TODO:
  }
}
