import { TouchType } from '../../../../../lib';
import { TouchInteraction } from '../TouchInteraction';
import { TouchEventBase,  } from './Base/TouchEventBase';

export { TouchType } from '../../../../../lib';
export class TouchEventNative extends TouchEventBase {
  constructor(touchInteraction: TouchInteraction) {
    super(touchInteraction);
    this.appendEvent();
  }
  protected appendEvent() {
    this.touchInteraction.model.modelViewWrapper.div.ontouchstart = (e: TouchEvent) => {
      // alert('eve');s
      // if (this.touchInteraction.model.appAnnotationModelTool) {
      if (
        this.touchInteraction.model.app.implementations.imageAnnoApp
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper
      ) {
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper.log.LogUpdate =
        //   'touch start';
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper.log.LogUpdate = `${e.touches[0].identifier}`;
      }
      // }
      const mEvent = this.touchInteraction.commonEventBuilder(e, TouchType.Start);
      this.touchInteraction.mouseEvent(mEvent);
    };
    this.touchInteraction.model.modelViewWrapper.div.ontouchend = (e: TouchEvent) => {
      if (
        this.touchInteraction.model.app.implementations.imageAnnoApp
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper
      ) {
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper.log.LogUpdate =
        //   'touch end';
      }
      const mEvent = this.touchInteraction.commonEventBuilder(e, TouchType.End);
      this.touchInteraction.mouseEvent(mEvent);
    };
    this.touchInteraction.model.modelViewWrapper.div.ontouchmove = (e: TouchEvent) => {
      // e.preventDefault(); //  it's common to call preventDefault() on touchmove rather than touchstart.
      if (
        this.touchInteraction.model.app.implementations.imageAnnoApp
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper
      ) {
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper.log.LogUpdate =
        //   'touch move';
      }
      const mEvent = this.touchInteraction.commonEventBuilder(e, TouchType.Move);
      this.touchInteraction.mouseEvent(mEvent);
    };
    this.touchInteraction.model.modelViewWrapper.div.ontouchcancel = (
      e: TouchEvent
    ) => {
      if (
        this.touchInteraction.model.app.implementations.imageAnnoApp
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper
      ) {
        // this.touchInteraction.model.app.implementations.annotationCanvasInstance.uiWrapper.log.LogUpdate =
        //   'touch cancel';
      }
      const mEvent = this.touchInteraction.commonEventBuilder(e, TouchType.Cancel);
      this.touchInteraction.mouseEvent(mEvent);
    };
  }
}
