
import {
  ActionType,
  Apps,
  InteractionMode,
  OptimizerTextleaderline,
  RendererType,
  Environment,
} from './lib';
import { Implementations } from './Implementations';
import { Model } from './Model';
import { Addon } from './Addon';
import { API } from './API';
export { InteractionMode } from './lib';
export interface IAnnotationImageParam {
  id: string;
  interactionMode: InteractionMode;
  UI: boolean;
  // eslint:disable-next-line:no-any
  theme?: unknown;
  themeUI?: any;
  renderer?: RendererType;
  rendererActive?: boolean;
  isLab?: boolean;
  environment: Environment;
  colorCover?: string;
  config: {
    BASE_URL: string;
  };
  features?: {
    mapFilter?: boolean;
    /** Point clustering feature */
    clustering?: boolean;
    hasAnimation?: boolean;
  };
}
export const CONFIG = {
  config: {
    BASE_URL: '', // placeholder value
  },
  PATH_ADDON: '../../../../Addon',
  VERSION_APP: '2',
  HAS_ANIMATION: false,
  LAB: false,
};
export class AppImageDrawing {
  private static _id = 0;
  public static get id() {
    return this._id++;
  }
  public CONFIG = CONFIG;
  public model: Model;
  public implementations: Implementations;
  public api: API;
  public addon: Addon;
  public param: IAnnotationImageParam;
  public APP: Apps;
  public PRODUCTION = true;
  public INSPECTION = false;
  // eslint:disable-next-line:no-any
  // viewController: any;
  public instanceID: number;
  public emitAction: (data: string) => void;
  public getNumLayers: () => number;
  public localizedString: (data: string) => string;
  public isLoaded = false;
  constructor(param: IAnnotationImageParam, dataDrawing: any, APP = Apps.AnnotationMap) {
    this.APP = APP;
    this.param = param;

    // console.log(this.param);
    if (this.INSPECTION) {
      this.PRODUCTION = false;
    }
    this.CONFIG.config = param.config;
    const needViewController = param.UI;
    if (param.features && param.features.hasAnimation) {
      this.CONFIG.HAS_ANIMATION = param.features.hasAnimation;
    }
    if (param.isLab) {
      this.CONFIG.LAB = param.isLab;
      this.CONFIG.HAS_ANIMATION = false;
    }
    this.instanceID = AppImageDrawing.id;

    this.api = new API(this);
    this.model = new Model(this, this.param.id, this.param.interactionMode);
    if (this.param.rendererActive === undefined || this.param.rendererActive === true) {
      this.param.rendererActive = true;
    } else {
      this.param.rendererActive = false;
    }
    this.addon = new Addon(this);

    this.initImplementations().then( () => {
      // this.param.renderer = RendererType.SVG;
      if (this.param.renderer === RendererType.Svg) {
        this.implementations.initAnnotationSVGInstance(
          this.CONFIG.HAS_ANIMATION,
          this.param.rendererActive as boolean,
          this.param.theme
        );
      } else {
        this.implementations.initAnnotationCanvasInstance(
          this.CONFIG.HAS_ANIMATION,
          this.param.rendererActive as boolean,
          this.param.theme
        );
        if (this.implementations.imageAnnoApp) {
          this.implementations.imageAnnoApp.activeCommandController.acProperties.isActivateSnapping = true;
        }
      }

      if (needViewController) {
        this.initViewController();
      }
      if (this.param.features && this.param.features.mapFilter) {
        // this.addon.initMapfilter();
      }

      this.initPost();

      // this.CONFIG.LAB = true;
      if (this.CONFIG.LAB) {
        this.initLab();
      }

      // console.log('dataDrawing', dataDrawing);
      if (dataDrawing.geometry && dataDrawing.geometry.length > 0) {
        // console.log(dataDrawing)
        if (this.implementations.imageAnnoApp) {
          this.implementations.imageAnnoApp.graphicsCoreWrapper.canvasWrapper.isActive = false;
          this.implementations.imageAnnoApp.geometryCommon.gcmdProcessing.DumpByIGeometriesFromImageDataModel(dataDrawing.geometry);
        }
        const absolute = true;
        const undoNeeded = false;
        this.api.setScaleGraphics(dataDrawing.scale, absolute, undoNeeded);
      }
        if (this.implementations.imageAnnoApp) {
          this.implementations.imageAnnoApp.scene.captureScene();
        }
     } );

     // for debugging test
    //  setTimeout(() => {
    //   this.implementations.imageAnnoApp?.activeCommandController.setActiveCommand(ActiveCommandMode.POINT_REGULAR);
    // }, 1000);
  }
  set setEnvironment(v: Environment) {
    this.param.environment = v;
  }
  async initImplementations() {
    return import('./Implementations').then(({ Implementations }) => {
        if (this.implementations) {return; }
        this.implementations =  new Implementations(
          this,
          this.model.modelViewWrapper.divNGraphicsHost
        );
        // this.viewController.nUIWrapper.nUI.ShiftTheme('black');
    }).catch(err => {
        console.debug('Stop loading mapticon.'); console.debug(err); throw err;
    });
}
  private initLab() {
    if (this.implementations.imageAnnoApp) {
      // this.implementations.annotationCanvasInstance.activeCommandController.ToggleCollisionDetection();
      // console.log('klklk');
      // this.implementations.annotationCanvasInstance.activeCommandController.acProperties.isActivateSnapping = true;
      // console.log(this.implementations.annotationCanvasInstance.activeCommandController.acProperties.isActivateSnapping);
    }
    // this.addon.InitMapticon();
    // this.addon.InitMapfilter();
    // this.addon.InitSmartDrawing();
    // this.addon.InitTourTool();
  }
  private initPost() {
    this.model.initPost();
    this.implementations.initPost();

    if (this.implementations.imageAnnoApp) {
      // this.implementation.annotationCanvasInstance.activeCommandController.ShiftLog();
    }

    if (this.param.renderer === RendererType.Svg) {
      // double check for the point event
      this.model.modelViewWrapper.divNGraphicsHost.style.pointerEvents = 'none';
    }

    if (this.model.imageWrapper.croppingDataTemp && this.implementations.imageAnnoApp) {
      this.implementations.imageAnnoApp.scene.paramsEditing.updateDataCropping(this.model.imageWrapper.croppingDataTemp);
    }
    if (this.model.imageWrapper.img) {
      if (this.param.colorCover) {
        this.model.imageWrapper.updateImg(this.model.imageWrapper.img, this.param.colorCover);
      } else {
        this.model.imageWrapper.updateImg(this.model.imageWrapper.img);
      }
    }
    this.isLoaded = true;

    this.initPostAppEnv();
  }
  private initPostAppEnv() {
    if (this.implementations.imageAnnoApp) {
      this.implementations.imageAnnoApp.activeCommandController.acProperties.isScaleDynamicRender = true;
      this.implementations.imageAnnoApp.activeCommandController.acProperties.optimizerTextleaderline = OptimizerTextleaderline.Collision;

      if (this.param.features && this.param.features.clustering) {
        this.implementations.imageAnnoApp.apiWrapper.environment.toggleClusteringPoint(this.param.features.clustering);
      }
    }
  }
  public restart() {
    // console.log('re start!!');
  }
  public toJson(): Record<string, unknown> {
    if (this.implementations.imageAnnoApp) {
      // this is a Map
      const geometries = this.implementations.imageAnnoApp.renderer.saveDrawingGeometries();
      // convert it to object literal
      return Array.from(geometries).reduce((obj, [, geometry]) => {
        const { id, type, nodes, data, styles } = geometry;
        obj[id] = {
          // required fields
          id, type, nodes,
          // optional fields
          ...(styles && Object.keys(styles).length && { styles }),
          ...(data && Object.keys(data).length && { data }),
        };
        return obj;
      }, {});
    }
    return {};
  }
  public pause(t = 0) {
    this.implementations.Pause(t);
  }
  public resume() {
    this.implementations.resume();
  }
  public destroy() {
    if (this.implementations) {
      this.implementations.destroy();
    }
    if (this.model) {
      this.model.destroy();
    }
    // this.emitAction(`destroy`);
    // this.emitAction(this.core.canvas.capture());
    // this. divHost.removeChild(this.core.div);
  }
  // { type: ActionTypeEnum; payload: unknown }
  public pushAction(v: string | { type: ActionType | string; payload: unknown }) {
    // console.log('v', v);
    // checking empty string and null value
    if (!v) {
      return;
    }
    this.model.pushAction(v);
  }
  // eslint:disable-next-line:no-any
  public pushTheme(v: unknown) {
    // console.log('pushTheme', v);
    this.model.pushTheme(v);
  }
  public bindActionEmitting(fn: (data: string) => void) {
    this.emitAction = fn;
  }
  public resize(width: number, height: number) {
    // this.graphicsCoreWrapper.ResizeCanvas(width, height);

    this.model.resize(width, height);
    this.implementations.resize(width, height);
    // if (this.viewController) {
    //   this.viewController.Resize(width, height);
    // }
  }
  async initViewController() {
    // return import('./ViewController')
    //   .then(({ ViewController }) => {
    //     if (this.viewController) {
    //       return;
    //     }
    //     this.viewController = new ViewController(this);
    //     this.viewController.nUIWrapper.nUI.ShiftTheme('black');
    //   })
    //   .catch((err) => {
    //     console.debug('Stop loading Annotation2D ViewController.');
    //     console.debug(err);
    //     throw err;
    //   });
  }
  public open() {
    this.model.ioWrapper.open();
  }
  // public updateImage(img: HTMLImageElement, dataCropping: string) {
  //   // document.append(img, dataCropping)
  //   this.model.modelViewWrapper.removeDivImg();
  //   // this.initImageByImg(img, dataCropping);
  // }
  public initImageByImg(img: HTMLImageElement, dataCropping: string, imgOriginal: HTMLImageElement, colorCover: string) {
    this.model.imageWrapper.initImageByImg(img, dataCropping, imgOriginal, colorCover);
  }
  public updateEffecting(d: any) {
    this.api.updateEffecting(d);
  }
  public initCropping() {
    this.addon.dynamicImport.initCropping();
  }
}
